<template>
  <b-modal :id="id" title="Product Detail" @ok="saveProduct" size="lg">
    <b-row>
      <b-col sm="12">
        <div class="alert alert-primary" role="alert">
          <template v-if="isCheckingCwPrice">
            <div class="spinner-border spinner-border-sm text-success" role="status">
              <span class="sr-only"></span>
            </div>&nbsp;&nbsp;Checking CW Price...
          </template>
          <template v-if="!cwMatchedProductChange">No change</template>
          <template v-if="cwMatchedProductChange">
            This product matched with CW product: [{{cwMatchedProduct.id}} - {{cwMatchedProduct.label}}]
            <br />
            Thumbnail: {{cwMatchedProduct.thumbnail}}
            <br />
            Price: {{cwMatchedProduct.price | currency(audConfig)}}
            <i
              class="fa fa-chevron-down"
              style="color: green"
              v-if="cwMatchedProduct.priceStatus == 'less'"
            ></i>
            <i
              class="fa fa-chevron-up"
              style="color: red"
              v-if="cwMatchedProduct.priceStatus == 'greater'"
            ></i>
            <i class="fa fa-check" v-if="cwMatchedProduct.priceStatus == 'equal'"></i>
            <br />
            <br />
            <b-button type="submit" size="sm" variant="success" @click="updateCwMatchedProduct()">
              <i class="fa fa-edit"></i> Update
            </b-button>
          </template>
        </div>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="8">
        <b-form-group>
          <label for="name">Name</label>
          <b-form-input type="text" id="name" placeholder="Enter product name" v-model="prod.name"></b-form-input>
        </b-form-group>
        <b-form-group>
          <label for="thumbnail">Thumbnail</label>
          <b-form-input
            type="text"
            id="thumbnail"
            placeholder="Enter thumbnail link"
            v-model="prod.thumbnail"
          ></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <img :src="prod.thumbnail" :alt="prod.name" width="150px" height="150px" />
      </b-col>
    </b-row>

    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label for="weight">Weight (g)</label>
          <b-form-input type="text" id="weight" placeholder="0" v-model="prod.weight"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="bvprice">BetterValue Price (A$)</label>
          <b-form-input type="text" id="bvprice" placeholder="0.00" v-model="prod.bvPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="cwprice">ChemistWarehouse Price (A$)</label>
          <b-form-input type="text" id="cwprice" placeholder="0.00" v-model="prod.cwPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group>
          <label for="shipprice">Shipment Price (A$)</label>
          <b-form-input type="text" id="shipprice" placeholder="0.00" v-model="prod.shipPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label>
            <b-form-checkbox
              v-model="prod.bvSalePricePick"
              @change="bvSalePricePickChanged"
            >BV Sale Price (A$)</b-form-checkbox>
          </label>
          <b-form-input type="text" id="bvsaleprice" placeholder="0.00" v-model="prod.bvSalePrice"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="4">
        <b-form-group>
          <label>
            <b-form-checkbox
              v-model="prod.cwSalePricePick"
              @change="cwSalePricePickChanged"
            >CW Sale Price (A$)</b-form-checkbox>
          </label>
          <b-form-input type="text" id="buyprice" placeholder="0.00" v-model="prod.cwSalePrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4"></b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="selprice">Selling Price (A$)</label>
          <b-form-input type="text" id="selprice" placeholder="0.00" v-model="prod.selPrice"></b-form-input>
        </b-form-group>
      </b-col>

      <b-col sm="4">
        <b-form-group>
          <label for="buyprice">Buying Price (A$)</label>
          <b-form-input type="text" id="buyprice" placeholder="0.00" v-model="prod.buyPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col sm="4">
        <b-form-group description="Buy. Price + Ship">
          <label for="oriprice">Ori. Price (A$)</label> -
          <vnd-price :value="prod.oriPrice"></vnd-price>
          <b-form-input type="text" id="oriprice" placeholder="0.00" v-model="prod.oriPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group description="Sel. Price + Commission(10%) + Ship + $2">
          <label for="sugprice">
            Sug. Price (A$) -
            <vnd-price :value="prod.sugPrice"></vnd-price>
          </label>
          <b-form-input placeholder="0.00" v-model="prod.sugPrice"></b-form-input>
        </b-form-group>
      </b-col>
      <b-col sm="4">
        <b-form-group>
          <label for="rndprice">
            Rnd. Price (VND) -
            <i style="color: red">{{prod.rndPrice | currency(vndConfig)}}</i>
          </label>
          <b-form-input type="text" placeholder="0.00" v-model="prod.rndPrice"></b-form-input>
        </b-form-group>
      </b-col>
    </b-row>
    <!-- <b-row>
      <iframe :src="prod.cwCheckPriceUrl"></iframe>
    </b-row>-->
  </b-modal>
</template>

<script>
import fb from "firebase";
import appSettings from "@/shared/appSettings";
const settings = appSettings.priceSettings;
import vndPrice from "../shared/vndPrice";
import { generateSubString } from "@/shared/utils";

export default {
  name: "ProductDetail",
  components: { vndPrice },
  props: ["item", "id"],
  data() {
    return {
      prod: {},
      show: false,
      settings: settings,
      cwMatchedProduct: {},
      cwMatchedProductChange: false,
      isCheckingCwPrice: false
    };
  },
  mounted: function() {},
  watch: {
    item: function(newValue) {
      this.prod = { ...newValue };
      this.$nextTick(() => {
        this.checkCWPrice();
      });
    },
    "prod.bvPrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwPrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.weight": function(newValue) {
      this.calculatePrices();
    },
    "prod.bvSalePrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwSalePrice": function(newValue) {
      this.calculatePrices();
    },
    "prod.bvSalePricePick": function(newValue) {
      this.calculatePrices();
    },
    "prod.cwSalePricePick": function(newValue) {
      this.calculatePrices();
    }
  },
  computed: {},
  methods: {
    saveProduct: function() {
      var prod = this.prod;
      prod.nameSearch = generateSubString(prod.name);
      if (!prod.name) return;
      if (prod.id) {
        return this.$fb.productCollection
          .doc(prod.id)
          .set(prod)
          .then(() => {
            this.$emit("productSaved", { ...prod });
            this.$bvModal.hide(this.id);
            return prod;
          });
      } else {
        var newProd = this.$fb.productCollection.doc();
        prod.id = newProd.id;
        return newProd.set(prod).then(() => {
          this.$fb.counterService.increaseProduct();
          this.$emit("productSaved", { ...prod });
          this.$bvModal.hide(this.id);
          return prod;
        });
      }
    },
    calculatePrices: function() {
      var {
        weight,
        bvPrice,
        cwPrice,
        bvSalePrice,
        cwSalePrice,
        bvSalePricePick,
        cwSalePricePick
      } = this.prod;
      this.prod.shipPrice = parseFloat(weight || 0) / 100;

      this.prod.selPrice = Math.max(bvPrice || 0, cwPrice || 0);
      this.prod.buyPrice = Math.min(bvPrice || 9999, cwPrice || 9999);

      this.calculateItself();

      //override if using sale price
      if ((cwSalePrice || 0) > 0 && cwSalePricePick) {
        this.prod.selPrice = cwSalePrice;
        this.prod.buyPrice = cwSalePrice;

        this.calculateItself();
      }
      if ((bvSalePrice || 0) > 0 && bvSalePricePick) {
        this.prod.selPrice = bvSalePrice;
        this.prod.buyPrice = bvSalePrice;

        this.calculateItself();
      }
    },
    calculateItself: function() {
      this.prod.oriPrice = parseFloat(this.prod.shipPrice) + parseFloat(this.prod.buyPrice);
      this.prod.sugPrice = parseFloat(
        this.prod.shipPrice + this.prod.selPrice * settings.commissionRate + settings.buyingFee
      ).toFixed(2);
      this.prod.sugPriceVND = parseFloat(this.prod.sugPrice * settings.vndExchageRate).toFixed(2);
    },
    bvSalePricePickChanged: function(checked) {
      if (checked) this.prod.cwSalePricePick = false;
    },
    cwSalePricePickChanged: function(checked) {
      if (checked) this.prod.bvSalePricePick = false;
    },
    checkCWPrice: function() {
      if (!this.prod || !this.prod.name) return;
      this.cwMatchedProduct = {};
      this.cwMatchedProductChange = false;
      this.isCheckingCwPrice = true;
      this.$http
        .get(this.appSettings.cwCheckPriceApi.replace("{term}", this.item.name))
        .then(res => {
          if (res && res.status == 200 && res.data) {
            var data = res.data;
            var validData = _.filter(data, item => parseFloat(item.id) > 0);
            if (validData.length == 1) {
              this.cwMatchedProduct = validData[0];
              this.cwMatchedProduct.price = parseFloat(this.cwMatchedProduct.Price);
              this.cwMatchedProduct.thumbnail = this.appSettings.cwProductThumbnailUrl150.replace(
                "{id}",
                this.cwMatchedProduct.id
              );
              var price = this.cwMatchedProduct.price;
              var cwPrice = parseFloat(this.prod.cwPrice);

              if (price > cwPrice + 0.5) {
                this.cwMatchedProduct.priceStatus = "greater";
              } else if (price < cwPrice - 0.5) {
                this.cwMatchedProduct.priceStatus = "less";
              } else {
                this.cwMatchedProduct.priceStatus = "equal";
              }

              this.cwMatchedProductChange =
                this.cwMatchedProduct.thumbnail != this.prod.thumbnail ||
                this.cwMatchedProduct.priceStatus != "equal";
            } else {
            } //todo: suggest for multiple matched product by name
          }

          this.isCheckingCwPrice = false;
        });
    },
    updateCwMatchedProduct: function() {
      if (this.cwMatchedProduct.thumbnail != this.prod.thumbnail) {
        this.prod.thumbnail = this.cwMatchedProduct.thumbnail;
      }
      if (this.cwMatchedProduct.priceStatus != "equal") {
        this.prod.cwPrice = parseFloat(this.cwMatchedProduct.price);
      }
    }
  }
};
</script>
